import React, { useEffect, useState } from 'react';
import { Octokit } from '@octokit/core';

import { Link, Grid, Column, Table } from '@carbon/react';

import yaml from 'js-yaml';


const octokitClient = new Octokit({});




const DataTable = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState();
  const [rows, setRows] = useState([]);

  const getRowItems = (entries) => {

    return Object.keys(entries).map((key) => {
      const entry = entries[key];
  
      return {
      // ...entry,
      case: key,
      appVersion: entry.latestAppVersion,
      caseVersion: entry.latestVersion
      // createdAt: new Date(row.created_at).toLocaleDateString(),
      // updatedAt: new Date(row.updated_at).toLocaleDateString(),
      // links: <LinkList url={row.html_url} homepageUrl={row.homepage} />,
      }
    });
  }

  useEffect(() => {
    async function getCarbonRepos() {
      // https://docs.github.com/en/rest/repos/contents?apiVersion=2022-11-28#get-repository-content
      const res = await octokitClient.request('GET /repos/{owner}/{repo}/contents/repo/case/index.yaml', {
        owner: 'IBM',
        repo: 'cloud-pak',
        // per_page: 75,
        // sort: 'updated',
        // direction: 'desc',
      });

      if (res.status === 200) {
        console.log(res.data);
        const decoded = atob(res.data.content);
        console.log(decoded);
        const j = yaml.load(decoded);
        console.log(j);
        setRows(getRowItems(j.entries));

      } else {
        console.log('Error obtaining repository data');
      }
    }

    getCarbonRepos();
  }, []);

  if (rows.length > 0) {
    return (
      <div>{JSON.stringify(rows)}</div>
      // <Pagination
      //   totalItems={rows.length}
      //   backwardText="Previous page"
      //   forwardText="Next page"
      //   pageSize={currentPageSize}
      //   pageSizes={[5, 10, 15, 25]}
      //   itemsPerPageText="Items per page"
      //   onChange={({ page, pageSize }) => {
      //     if (pageSize !== currentPageSize) {
      //       setCurrentPageSize(pageSize);
      //     }
      //     setFirstRowIndex(pageSize * (page - 1));
      //   }}
    // />
    )
  } else {
    <span>no data</span>
  }

}

export default DataTable;
